<template>
  <main>
    <h1>Upload new File!</h1>
    <p>Select the file you want to send and the consumer whose will receive the file</p>

    <form
      enctype="multipart/form-data"
      method="POST"
      @submit.prevent
      @submit.capture="checkCheckBoxes()"
      @submit="submitForm()"
    >
      <div>
        <label for="fileField">
          <img src="download_button.png" alt="download_button" />
          <span>Click to add File</span>
          <input type="file" name="fileToUpload" id="fileField" @change="checkFile()" required />
        </label>
        <input
          type="text"
          id="filenameField"
          name="filename"
          v-model="file.name"
          placeholder="Enter filename"
        />
        <input type="submit" value="SEND" />
      </div>
      <div>
        <table>
          <thead>
            <tr>
              <th>Username</th>
              <th>Email</th>
              <th>Name &amp; Surname</th>
              <th>Select</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(consumer, index) in file.consumers" v-bind:key="index">
              <td v-if="index!==file.consumers.length-1">{{consumer.username}}</td>
              <td v-else>
                <input
                  type="text"
                  v-model="consumer.username"
                  @keyup="checkUsername(consumer.username)"
                  @keydown.enter.prevent
                />
              </td>
              <td v-if="index!==file.consumers.length-1">{{consumer.email}}</td>
              <td v-else>
                <input type="text" v-model="consumer.email" @keydown.enter.prevent />
              </td>
              <td v-if="index!==file.consumers.length-1">{{consumer.nameSurname}}</td>
              <td v-else>
                <input type="text" v-model="consumer.nameSurname" @keydown.enter.prevent />
              </td>
              <td>
                <input
                  type="checkbox"
                  v-bind:name="consumer.username"
                  v-model="consumer.checked"
                  v-bind:value="consumer.username"
                />
              </td>
            </tr>
          </tbody>
        </table>
        <h2>Add hashtags</h2>
        <div>
          <input
            type="text"
            v-model="newHashtag"
            v-on:keydown.enter.prevent
            v-on:keyup.enter="addHashtag()"
            placeholder="Add Hashtag"
          />
          <button type="button" @click="addHashtag">
            <img src="add_button.png" alt="add_button" />
          </button>
        </div>

        <ul>
          <li v-for="hashtag in file.hashtags" :key="hashtag">
            <Hashtag class="hashtags" v-bind:hashtag="hashtag" @delete-hashtags="deleteHashtags" />
          </li>
        </ul>
      </div>
    </form>
  </main>
</template>

<script>
import Axios from "axios";
import Hashtag from "../components/Hashtag";

export default {
  name: "AddFile",
  components: {
    Hashtag,
  },

  data: function () {
    return {
      file: {
        name: "",
        hashtags: [],
        content: "",
        consumers: [],
      },
      newHashtag: "",
    };
  },

  watch: {
    "file.name": function () {
      this.file.name = this.file.name.replace(" ", "_");
    },
    newHashtag: function () {
      this.newHashtag = this.newHashtag.replace(" ", "_");
    },
  },

  methods: {
    checkUsername: function (username) {
      let regex = /^[a-zA-Z]{6}[0-9]{2}[abcdehlmprstABCDEHLMPRST]{1}[0-9]{2}([a-zA-Z]{1}[0-9]{3})[a-zA-Z]{1}$/i;
      if (regex.exec(username)) {
        document.querySelector("input[type='submit']").disabled = false;
      } else {
        document.querySelector("input[type='submit']").disabled = true;
      }
    },

    checkFile: function () {
      //get the input file element
      let fileField = document.getElementById("fileField");
      //get the label of the input file element
      let fileFieldLabel = document.querySelector("label[for='fileField']");

      //check if the user have chosen a file
      if (fileField.value === "") {
        //change background color to white if the user has removed the file
        fileFieldLabel.style.backgroundColor = "white";
        //set the input file name to empty string
        this.file.name = "";
      } else {
        //set background color to black if the user select a file
        fileFieldLabel.style.backgroundColor = "rgb(65, 65, 65)";
        //get the start index of the filename
        let startIndex = fileField.value.lastIndexOf("\\") + 1;
        this.file.name = fileField.value.substring(startIndex);

        //get binary data as base64
        let fileReader = new FileReader();
        fileReader.readAsDataURL(
          document.querySelector("input[type='file']").files[0]
        );
        let that = this;
        fileReader.onload = function () {
          let startIndex = fileReader.result.indexOf(",") + 1;
          that.file.content = fileReader.result.substring(startIndex);
        };
      }
    },

    addHashtag: function () {
      if (this.newHashtag != "") {
        this.file.hashtags.push(this.newHashtag);
        this.newHashtag = "";
      } else {
        event.target.parentElement.parentElement.querySelector(
          "input[type='text']"
        ).style.borderColor = "red";
        event.target.parentElement.parentElement.querySelector(
          "input[type='text']"
        ).placeholder = "insert an hashtag";
      }
    },

    deleteHashtags: function (hashtag) {
      this.file.hashtags = this.file.hashtags.filter(
        (oldHashtag) => oldHashtag !== hashtag
      );
    },

    checkCheckBoxes: function () {
      let check = false;
      this.file.consumers.forEach((element) => {
        if (element.checked === true) {
          check = true;
        }
      });
      if (check === false) {
        alert("You have to select at least one consumer");
        event.preventDefault();
      }
    },

    /*submitForm: function () {
      let queryString = this.consumers
        .filter(
          (element) => element.checked === true && element.username !== ""
        )
        .map((element) => {
          return element["username"];
        })
        .join("+");

      Axios.post(
        `${process.env.VUE_APP_APIROOT}uploaderarea/filemanagment?consumers=${queryString}`,
        this.file
      )
        .then((response) =>
          console.log(`Il server ha risposto con: ${response.status}`)
        )
        .catch((error) => console.log(error));
    },*/
    submitForm: function () {
      let send = {
        content: "",
        hashtags: [],
        name: "",
        consumers: [],
      };
      send.content = this.file.content;
      send.hashtags = this.file.hashtags;
      send.name = this.file.name;
      send.consumers = this.file.consumers.filter(
        (consumer) => consumer.checked === true
      );
      Axios.post(
        `${process.env.VUE_APP_APIROOT}uploaderarea/file`,
        JSON.stringify(send),
        {
          headers: {
            "Content-type": "text/plain",
          },
        }
      )
        .then((response) =>
          console.log(`Il server ha risposto con: ${response.status}`)
        )
        .catch((error) => console.log(error));
    },
  },

  created: function () {
    Axios.get(`${process.env.VUE_APP_APIROOT}uploaderarea/consumers`)
      .then((response) => {
        let array = response.data.map((element) => ({
          username: element.username,
          email: element.email,
          nameSurname: element.nameSurname,
        }));
        array.forEach((element) => (element.checked = false));
        let newConsumer = {
          username: "",
          email: "",
          nameSurname: "",
          checked: false,
        };
        array.push(newConsumer);
        this.file.consumers = array;
        this.newConsumer = this.file.consumers[1].username;
      })
      .catch((error) => console.error(error));
  },
};
</script>

<style scoped>
h1 {
  text-align: center;
  font-size: 3em;
}

form {
  margin-right: auto;
  margin-left: auto;
  width: 90%;
  display: flex;
  flex-flow: row nowrap;
  height: 65vh;
}

form > div:first-of-type {
  flex-basis: 30%;
  display: flex;
  flex-flow: column nowrap;
}

form > div:last-of-type {
  flex-basis: 70%;
  display: flex;
  flex-flow: column nowrap;
  align-items: flex-end; /*how to align internal items horizzontally */
}

form > div:first-of-type > label[for="fileField"] {
  background-color: white;
  border-style: dashed;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 4vh;
  width: 25vw;
  text-align: center;
  justify-content: space-around;
  flex-basis: 45%;
  display: flex;
  flex-flow: column nowrap;
}

form > div:first-of-type > label[for="fileField"]:hover {
  cursor: pointer;
  background-color: rgb(65, 65, 65);
  border-style: solid;
}

form > div:first-of-type > label[for="fileField"] img {
  width: 23vh;
  max-width: 25vw;
  height: auto;
  display: block;
  margin-left: auto;
  margin-right: auto;
  flex-basis: 66%;
}

form > div:first-of-type > label[for="fileField"] span {
  flex-basis: 5%;
  color: rgb(104, 104, 104);
}

form > div:first-of-type > label input[type="file"] {
  display: none;
}

form > div:first-of-type > input[type="text"] {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 4vh;
  flex-basis: 10%;
  width: 25vw;
}

form > div:first-of-type > input[type="submit"] {
  margin-right: auto;
  margin-left: auto;
  width: 25vw;
  flex-basis: 15%;
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0.83);
  color: rgb(214, 139, 0);
  font-weight: bolder;
  font-size: 5vh;
  border: 3px solid black;
}

form > div:first-of-type > input[type="submit"]:hover {
  cursor: pointer;
  color: orange;
}

form > div:first-of-type > input[type="submit"]:disabled {
  background-color: rgb(206, 206, 206);
  color: rgb(160, 160, 160);
  border: 1px solid grey;
  cursor: not-allowed;
  font-weight: lighter;
}

form > div:last-of-type > table {
  box-sizing: border-box;
  flex-basis: 50%;
  width: 90%;
  display: flex;
  flex-flow: column nowrap;
  text-align: center;
  overflow: auto;
  border: 2px solid black;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

form > div:last-of-type > table thead {
  flex-basis: 15%;
  width: 100%;
  display: flex;
  flex-flow: column nowrap;
  background-color: rgba(0, 0, 0, 0.83);
  color: rgb(214, 139, 0);
}

form > div:last-of-type > table thead tr {
  display: flex;
  flex-flow: row nowrap;
  flex-basis: 100%;
}

form > div:last-of-type > table tbody tr:nth-child(odd) {
  background-color: rgb(230, 230, 230);
}

form > div:last-of-type > table thead tr th {
  flex-basis: 25%;
}

form > div:last-of-type > table tbody {
  flex-basis: 90%;
  display: flex;
  flex-flow: column nowrap;
  max-height: 40vh;
  overflow-y: auto;
}

form > div:last-of-type > table tbody tr {
  flex-basis: 15%;
  display: flex;
  flex-flow: row nowrap;
}

form > div:last-of-type > table tbody tr td {
  flex-basis: 25%;
  text-align: center;
  justify-content: center;
}

form > div:last-of-type > table tbody tr td input[type="text"] {
  width: 90%;
  text-align: center;
  border: 0;
  border-bottom: 1px solid black;
}

form > div:last-of-type > h2 {
  margin: 1vh auto;
  flex-basis: 10%;
}

form > div:last-of-type > div {
  flex-basis: 10%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  width: 50%;
}

form > div:last-of-type > div input[type="text"] {
  box-sizing: border-box;
  flex-basis: 90%;
  margin: 0;
  padding: 0;
  border: 1px solid grey;
  border-radius: 3px;
}

form > div:last-of-type > div button[type="button"] {
  box-sizing: border-box;
  flex-basis: 10%;
  height: 100%;
  padding: 0px;
  margin: 0px;
}

form > div:last-of-type > div button[type="button"] img {
  box-sizing: border-box;
  width: 100%;
  height: auto;
  vertical-align: middle;
}

form > div:last-of-type > ul {
  flex-basis: 30%;
  margin: 0 auto;
  border: 0;
  padding: 0;
  width: 50%;
  max-height: 10vh;
  overflow-y: auto;
  display: flex;
  flex-flow: column nowrap;
}

form ul li {
  list-style: none;
  flex-basis: 9%;
}

::-webkit-scrollbar {
  display: none;
}
</style>